// import Axios from "axios";
import { Add, Remove } from "./database";

import("lazysizes");
import("lazysizes/plugins/parent-fit/ls.parent-fit");
import("./components/_card");
import("./components/_link");
import("./components/_navigation");
import("./components/_skip");
import("./components/_menu");

let DetailData;

const getDatailData = async () => {
	const params = new Proxy(new URLSearchParams(window.location.search), {
		get: (searchParams, prop) => searchParams.get(prop),
	});
	const value = params.id;
	const spinner = document.getElementById("spinner");
	if (value !== undefined && value !== null) {
		try {
			import("axios")
			.then((module) => module.default)
			.then(async(Axios) => {
				spinner.removeAttribute("hidden");
				await Axios(`https://restaurant-api.dicoding.dev/detail/${value}`).then((response) => {
					const data = response.data.restaurant;
					DetailData = data;
					const dataProp = Object.getOwnPropertyNames(data);
					for (let i = 0; i < dataProp.length; i++) {
						const el = dataProp[i];
						if (document.querySelector(`#${el}`) !== null) {
							document.querySelector(`#${el}`).innerText = data[el];
						}
					}
					document.querySelector(`meta[name="description"]`).setAttribute("content", data.description);
					document.querySelector(`#id`).value = data.id;
					document
						.querySelector(`#bg-image`)
						.setAttribute("src", `https://restaurant-api.dicoding.dev/images/small/${data.pictureId}`);
					document
						.querySelector(`#image`)
						.setAttribute("src", `https://restaurant-api.dicoding.dev/images/small/${data.pictureId}`);
					data.menus.foods.forEach((food) => {
						document.querySelector(`#foods ul`).innerHTML += `<li>${food.name}</li>`;
					});
					data.menus.drinks.forEach((drink) => {
						document.querySelector(`#drinks ul`).innerHTML += `<li>${drink.name}</li>`;
					});
					data.categories.forEach((category) => {
						document.querySelector(`#category ul`).innerHTML += `<li>${category.name}</li>`;
					});
					document.querySelector("#countReviwer").innerText = data.customerReviews.length;
					data.customerReviews.forEach((customer) => {
						document.querySelector(`.review`).innerHTML += `<div class="comment">
						<h4 class="text-primary">${customer.name}</h4>
						<small>${customer.date}</small>
						<p id="review-comment">${customer.review}</p>
					</div>`;
					});
				});
				spinner.setAttribute("hidden", true);
			});
		} catch (error) {
			import("sweetalert2")
				.then((module) => module.default)
				.then((Swal) => {
					Swal.fire({
						icon: "error",
						title: "Oops...",
						text: error,
					});
				});
			spinner.setAttribute("hidden", true);
		}
	}
};

const favorite = document.querySelector("#favorite");
if (favorite !== undefined && favorite !== null) {
	const params = new Proxy(new URLSearchParams(window.location.search), {
		get: (searchParams, prop) => searchParams.get(prop),
	});
	favorite.addEventListener("click", () => {
		if (favorite.classList.contains("active") && favorite.innerText === "Favorited") {
			Remove(params.id);
			import("sweetalert2")
			.then((module) => module.default)
			.then((Swal) => {
				Swal.fire({
					icon: "success",
					title: "Success",
					text: ``,
					html: `Berhasil menghapus data <strong>${DetailData.name}</strong> didalam favorite`,
				});
			});
			favorite.classList.toggle("active");
			favorite.innerText = "Favorite";
		} else {
			Add(DetailData);
			import("sweetalert2")
			.then((module) => module.default)
			.then((Swal) => {
				Swal.fire({
					icon: "success",
					title: "Success",
					text: ``,
					html: `Berhasil menambahkan data <strong>${DetailData.name}</strong> ke dalam favorite`,
				});
			});
			favorite.classList.toggle("active");
			favorite.innerText = "Favorited";
		}
	});
}
const Like = document.querySelector("#like");
if (Like !== undefined && Like !== null) {
	const params = new Proxy(new URLSearchParams(window.location.search), {
		get: (searchParams, prop) => searchParams.get(prop),
	});
	Like.addEventListener("click", () => {
		if (Like.classList.contains("active") && Like.innerText === "Liked") {
			Remove(params.id, 1);
			import("sweetalert2")
			.then((module) => module.default)
			.then((Swal) => {
				Swal.fire({
					icon: "success",
					title: "Success",
					text: ``,
					html: `Berhasil menghapus data <strong>${DetailData.name}</strong> didalam Like`,
				});
			});
			Like.setAttribute("data-liked", false);
			Like.classList.toggle("active");
			Like.innerText = "Like";
		} else {
			Add(DetailData, 1);
			import("sweetalert2")
			.then((module) => module.default)
			.then((Swal) => {
				Swal.fire({
					icon: "success",
					title: "Success",
					text: ``,
					html: `Berhasil menambahkan data <strong>${DetailData.name}</strong> ke dalam Like`,
				});
			});
			Like.setAttribute("data-liked", true);
			Like.classList.toggle("active");
			Like.innerText = "Liked";
		}
	});
}

const getData = async () => {
	const spinner = document.getElementById("spinner");
	const html = document.getElementById("restaurants");
	const listFavorite = document.getElementById("listFavorite");
	if (html !== undefined && html !== null) {
		try {
			import("axios")
			.then((module) => module.default)
			.then(async(Axios) => {
				const configs = {
					method: "GET",
					url: "https://restaurant-api.dicoding.dev/list",
				};
				spinner.removeAttribute("hidden");
				await Axios(configs).then(function (response) {
					const data = response.data.restaurants;
					data.forEach((el) => {
						html.innerHTML += ` 
			<x-card 
				name="${el.name}" 
				city="${el.city}" 
				id="${el.id}"
				image="https://restaurant-api.dicoding.dev/images/small/${el.pictureId}" rating="${el.rating}"
				desc="${el.description.substring(0, 100)}" 
			/>`;
					});
				});
				spinner.setAttribute("hidden", true);
			});
		} catch (error) {
			import("sweetalert2")
			.then((module) => module.default)
			.then((Swal) => {
				Swal.fire({
					icon: "error",
					title: "Oops...",
					text: error,
				});
			});
			spinner.setAttribute("hidden", true);
		}
	}
	if (listFavorite !== undefined && listFavorite !== null) {
		try {
			spinner.removeAttribute("hidden");
			const request = window.indexedDB.open("ZEED_RESTO", 1);
			request.onsuccess = (ev) => {
				const db = ev.target.result;
				const transaction = db.transaction(["Favorite"], "readonly");
				const objectStore = transaction.objectStore("Favorite");
				objectStore.openCursor().onsuccess = (ev) => {
					const cursor = ev.target.result;
					const el = cursor.value;
					listFavorite.innerHTML += ` 
					<x-card 
						name="${el.name}" 
						city="${el.city}" 
						id="${el.id}"
						image="https://restaurant-api.dicoding.dev/images/small/${el.pictureId}" rating="${el.rating}"
						desc="${el.description.substring(0, 100)}" 
					/>`;
					if (cursor) {
						cursor.continue();
					}
				};
			};
			spinner.setAttribute("hidden", true);
		} catch (error) {
			import("sweetalert2")
			.then((module) => module.default)
			.then((Swal) => {
				Swal.fire({
					icon: "error",
					title: "Oops...",
					text: error,
				});
			});
			spinner.setAttribute("hidden", true);
		}
	}
};

getData();
getDatailData();
