import { Database } from "./config/config";

const request = window.indexedDB.open(Database.name, 1);
request.onupgradeneeded = async (ev) => {
	const db = await ev.target.result;
	await db.createObjectStore(Database.table[0], { keyPath: "id", unique: true });
	await db.createObjectStore(Database.table[1], { keyPath: "id", unique: true });
	await db.close();
};

export const Add = (data, index = 0) => {
	const request = window.indexedDB.open(Database.name, 1);
	request.onsuccess = async (ev) => {
		const db = await ev.target.result;
		const transaction = await db.transaction([Database.table[index]], "readwrite");
		const objectStore = await transaction.objectStore(Database.table[index]);
		await objectStore.add(data);
		await db.close();
		return true;
	};
	return data;
};

export const Remove = (id, index = 0) => {
	const request = window.indexedDB.open(Database.name, 1);
	request.onsuccess = async (ev) => {
		const db = await ev.target.result;
		const transaction = await db.transaction([Database.table[index]], "readwrite");
		const objectStore = await transaction.objectStore(Database.table[index]);
		const indexDB = await objectStore.delete(id);

		indexDB.onsuccess = () => {
			return true;
		};

		indexDB.onerror = () => {
			console.error("Gagal menghapus item.");
		};
		db.close();
		return true;
	};
	request.onerror = async () => {
		return false;
	};
};

export const GetAll = (index = 0) => {
	const request = window.indexedDB.open(Database.name, 1);
	request.onsuccess = async (ev) => {
		const db = await ev.target.result;
		const transaction = await db.transaction([Database.table[index]], "readonly");
		const objectStore = await transaction.objectStore(Database.table[index]);
		objectStore.openCursor().onsuccess = async (ev) => {
			const cursor = await ev.target.result;
			if (cursor) {
				cursor.continue();
			}
		};
		db.close();
		return true;
	};
	request.onerror = async () => {
		return false;
	};
};

export const Get = (id, index) => {
	const request = window.indexedDB.open(Database.name, 1);
	request.onsuccess = (event) => {
		const db = event.target.result;
		const transaction = db.transaction([Database.table[index]], "readonly");
		const objectStore = transaction.objectStore(Database.table[index]);
		const indexDB = objectStore.get(id);

		indexDB.onsuccess = (event) => {
			return event.target.result
		};

		indexDB.onerror = (event) => {
			console.error("Failed to retrieve object:", event.target.error);
		};
		db.close();
	};
};
